@import url(https://fonts.googleapis.com/css?family=Pacifico);
@import url(https://fonts.googleapis.com/css?family=Lora:400,400i,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
  margin-left: 80px;
  font-weight: 100;
  font-size: 24px;
}

.search {
  margin-right: 80px;
  font-weight: 500;
  font-size: 16px;
}

/* Global Styles */

.flex-direction-column {
  flex-direction: column;
}

.big-quote {
  font-size: 2.4em;
  line-height: 1.4em;
}

body {
  font-family: "Pacifico", cursive;
}

/* Filler, can remove later. */
.module {
  /* border: 1px solid black; */
  height: 100%;
  min-width: 350px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding-left: 5vw;
  padding-right: 1vw;
  dfwidth: auto;
}

.first-module-margin {
  margin-left: 10vh;
}

.text-align-right {
  text-align: right;
}

.text-align-center {
  text-align: center;
}

.text-bold {
  font-weight: bold;
}

.text-lighter {
  font-weight: lighter;
}

.big-image-container {
  overflow: hidden;
  height: 90%;
  max-width: auto;
}

.big-image {
  object-fit: contain;
  margin-top: 5%;
  height: 100%;
  width: auto;
  will-change: transform;
}

.big-image-caption {
  color: gray;
  padding-top: 5%;
  text-align: center;
  font-size: 1.2em;
  line-height: 1.5em;
  width: 110%;
}

.setup-hook {
  height: 80%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-top: 20%;
  /* padding-right: 20%; */
}

.setup {
  margin-bottom: 10%;
  font-size: 1.2em;
}

.hook {
  font-size: 1.8em;
  margin-bottom: auto;
}

.short-paragraph {
  font-size: 1.2em;
  line-height: 1.4em;
  max-width: 600px;
}

.issueTitle {
  font-weight: 700;
  margin-left: 80px;
}

.articleLinks {
  display: flex;
  list-style: none;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
  margin-left: 80px;
}

li {
  flex-grow: 0;
  margin-right: 84px;
}

button {
  white-space: nowrap;
  background: none;
  font-size: 16px;
  border: 0px;
}

button:focus {
  outline: none;
}

.selected {
  font-weight: 400;
}

.unselected {
  font-weight: 100;
}

html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Lora", serif;
}

.viewport {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  /* border: 2px solid;
  border-color: black; */

  /* flex for children */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  /* flex position within parent */
  flex: 0 0 50px;
  width: 100%;
}

.article {
  /* flex for children */
  display: flex;
  flex-direction: row;
  overflow: auto;

  /* flex position within parent */
  flex: 1 1;
  padding-left: 80px;
}

.footer {
  /* border: 2px solid;
  border-color: black; */

  margin-bottom: 10px;

  /* flex position within parent */
  flex: 0 0 100px;
  width: 100%;
}

