@import url("https://fonts.googleapis.com/css?family=Lora:400,400i,700");

html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Lora", serif;
}

.viewport {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  /* border: 2px solid;
  border-color: black; */

  /* flex for children */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  /* flex position within parent */
  flex: 0 0 50px;
  width: 100%;
}

.article {
  /* flex for children */
  display: flex;
  flex-direction: row;
  overflow: auto;

  /* flex position within parent */
  flex: 1;
  padding-left: 80px;
}

.footer {
  /* border: 2px solid;
  border-color: black; */

  margin-bottom: 10px;

  /* flex position within parent */
  flex: 0 0 100px;
  width: 100%;
}
